define('app.module',[
    "angular",
    "module",
    "./core.module",
    "components/mainNavigation/mainNavigation.module",
    "components/features/features.module"
], function(angular, module) {
    "use strict";

    routeConfig.$inject = ["$httpProvider", "$locationProvider", "$stateProvider", "$urlRouterProvider"];

    function routeConfig($httpProvider, $locationProvider, $stateProvider, $urlRouterProvider) {
        $httpProvider.defaults.headers.common["AUTH_TOKEN"] = "myAuthCode"
        $locationProvider.html5Mode(true);
        $urlRouterProvider.otherwise("teamManager");

        $stateProvider.state("teamManager", {
            url: "/teamManager",
            templateUrl: "resources/app/components/features/teamManager/teamManager.html",
            controller: "TeamManagerController",
            controllerAs: "vm"
        }).state("userManager", {
            url: "/userManager",
            templateUrl: "resources/app/components/features/userManager/userManager.html",
            controller: "UserManagerController",
            controllerAs: "vm"
        });
    }

    //The Main App Module
    return angular.module("app", [
        // Required App Modules
        "app.core",
        "app.mainNavigation",
        "app.features"
    ]).config(routeConfig).constant("spatGlobals", module.config().spatGlobals);
});

